*{
    margin: 0;
    padding: 0;
}
body{
background-color: rgb(0, 0, 0,0.9);
overflow-x: hidden;
}
::selection{
    color: #000000;
}
h1{
    text-align: center;
    font-size: 25px;
    text-transform: uppercase;
    color: #fff;
    font-weight: bold;
    text-shadow: 0 12px 5px #262626;
}
h2{
    text-align: center;
    font-size: 55px;
    letter-spacing: 2px;
    text-transform: uppercase;
    font-weight: bold;
    color: #fff;
    text-shadow: 0 12px 5px #262626;
}
.card,
.card-1{
    height: 200px;
    width: 100%;
    margin: 20px auto;
    box-shadow: 5px 8px 3px hsl(80, 100%, 99%);
    overflow: hidden;
    -webkit-transition: all.4s ease-in-out;
    transition: all.4s ease-in-out;
}
.cardText{
    padding-top: 20px;
}
.card:hover,
.card-1:hover{
    -webkit-transform: scale(1);
    transform:scale(1.06)
}
.card-1{
    background-image: url('./image/india.png');
  background-repeat: repeat;
  
}

#color{
    font-size: 16px;
    color: #000000;

}




@media only screen and (max-width: 979px){
    h1{
font-size: 30px;
    }
    h2{
        font-size: 27px;
            }
    #color{
        font-size: 12px;
        color: #000000;
    
    }
 
}
